<template>
    <div>
        <van-nav-bar
                left-arrow
                @click-left="$router.go(-1)"
                title="登录密码"
        ></van-nav-bar>
        <div>
            <van-field
                    style="margin-top: 3vw;"
                    label="旧登录密码"
                    v-model="old_password"
                    placeholder="请输入旧的登录密码"
            ></van-field>
            <van-field
                    label="新登录密码"
                    v-model="new_password"
                    placeholder="请输入新的登录密码"
            ></van-field>
            <van-field
                    label="确认新密码"
                    v-model="confirm_password"
                    placeholder="请再次输入新密码"
            ></van-field>

            <van-field
                    style="margin-top: 3vw;"
                    label="手机号"
                    v-model="phone"
                    readonly
                    disabled
            ></van-field>
            <van-field
                    label="验证码"
                    v-model="sms"
                    placeholder="请输入验证码"
            >
                <a slot="button" style="color: #098DF2;">获取验证码</a>
            </van-field>
        </div>
        <van-button style="width: 90vw;margin-top: 10vw;" type="info">确认</van-button>
    </div>
</template>

<script>
    import { NavBar, Field, Button } from 'vant'

    export default {
        name: "loginPassword",
        components: {
            [NavBar.name]: NavBar,
            [Field.name]: Field,
            [Button.name]: Button,
        },
        data(){
            return {
                old_password: '',
                new_password: '',
                confirm_password: '',
                sms: '',
            }
        },
        computed:{
            phone(){
                return this.$store.state.userInfo.phone.slice(0, 4)+'***'+this.$store.state.userInfo.phone.slice(this.$store.state.userInfo.phone.length-4)
            }
        }
    }
</script>

